// Formidable Forms plugin is used for the Suggest a Resource form.
// We've turned off the plugin stylesheet. These styles compliment
// and adjust the basic form element styles from Decanter.

.frm_form_fields {

  fieldset {
    border: 0;
    margin: 0;
    padding: 0;
  }

  .frm_form_field:not(.form-heading) {
    @include responsive-spacing('margin', null null 1);
  }

  .frm_primary_label {
    @include responsive-spacing('margin', null null -2);
    font-weight: $su-font-semi-bold;
  }

  .frm_required {
    color: $su-color-bright-red;
  }

  input, textarea, select, .wp-editor-container {
    border-color: #6b7280;
    max-width: none;

    &:focus {
      border-color: $su-color-bright-blue;
      border-width: 2px;
      box-shadow: none;
    }
  }

  .frm_radio,
  .frm_checkbox {
    @include responsive-spacing('margin', null null -2);

    + .frm_radio {
      @include responsive-spacing('margin', null null -2);
    }

    label {
      font-weight: $su-font-regular;
    }
  }

  .frm_checkbox {
    margin-bottom: 0;
  }

  .frm_description {
    @include modular-typography(-1);
  }

  input[type=radio], input[type=checkbox] {
    appearance: none;
    border-width: 1px;
    border-color: #6b7280;
    display: inline-block;
    margin: 0;
    height: 2.4rem;
    width: 2.4rem;
    vertical-align: middle;

    &:focus {
      box-shadow: rgba($su-color-white, 1) 0px 0px 0px 2px, rgba($su-color-bright-blue, 1) 0px 0px 0px 4px, rgba($su-color-black, 0) 0px 0px 0px 0px;
    }

    &:checked {
      background-color: $su-color-bright-blue;
      background-position: center;
      background-repeat: no-repeat;
      background-size: 100% 100%;
      border-color: transparent;
    }
  }

  input[type=radio] {
    border-radius: 100rem;

    &:checked {
      background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='white' xmlns='http://www.w3.org/2000/svg'%3e%3ccircle cx='8' cy='8' r='3'/%3e%3c/svg%3e");
    }
  }

  input[type=checkbox] {
    margin-right: .5rem;

    &:hover {
      filter: brightness(100%);
    }

    &:checked {
      background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='white' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M12.207 4.793a1 1 0 010 1.414l-5 5a1 1 0 01-1.414 0l-2-2a1 1 0 011.414-1.414L6.5 9.086l4.293-4.293a1 1 0 011.414 0z'/%3e%3c/svg%3e");

      &:after {
          display: none;
      }

      &:focus {
          background-color: $su-color-bright-blue;
      }
    }
  }

  .wp-editor-tabs {

    button {
      color: $su-color-black;
      border-color: #6b7280;
      border-style: solid;
      border-left-width: 1px;
      border-top-width: 1px;
      border-right-width: 1px;

      &:focus,
      &:hover {
        box-shadow: none;
      }
    }
  }

  .tmce-active {

    .switch-tmce {
      background-color: $su-color-black-20;
    }

    .switch-html {
      background: $su-color-white;
    }
  }

  .html-active {

    .switch-tmce {
      background-color: $su-color-white;
    }

    .switch-html {
      background: $su-color-black-20;
    }

    .ed_button {
      background-color: $su-color-white;
      border-color: #2271b1;
      height: auto;
      width: auto;
    }
  }

  .mce-toolbar-grp,
  .quicktags-toolbar {
    background-color:  $su-color-black-20;
  }

  .mce-container-body button {
    background-color: $su-color-white;
  }

  // Form error styles.
  .frm_blank_field {

    label,
    .frm_error {
      color: $su-color-bright-red;
      font-weight: $su-font-semi-bold;
    }

    input {
      border-color: $su-color-bright-red;
    }

    .mce-edit-area iframe {
      border: 1px solid $su-color-bright-red;
    }
  }

  .frm_form_field {

    &.frm_one_col .frm_opt_container,
    &.frm_two_col .frm_opt_container,
    &.frm_three_col .frm_opt_container,
    &.frm_four_col .frm_opt_container {
      grid-gap: 1rem;
      margin-bottom: 1rem;
    }

    input[readonly="readonly"] {
      color: $su-color-black-70;
    }
  }
}

.frm_error_style {
  @include responsive-spacing('margin', null null -1);
  background-color: $su-color-bright-red;
  color: $su-color-white;
  padding: 2rem;
}

// Styles for a single-select, autocomplete field.
// This will need to be updated for multiselect if
// we ever need that.
.chosen-container-single {

  .chosen-single {
    display: block;
    position: relative;
    height: 4.8rem;
    border: 1px solid #6b7280;
    padding: 1rem 0.7em;
    text-decoration: none;
    font-weight: $su-font-regular;
    color: $su-color-black;
    width: 100% !important;

    span {
      display: block;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }

    &.chosen-single-with-deselect {

      span {
        margin-right: 35px;
      }
    }

    // The icon/button to remove the current selection.
    abbr {
      display: block;
      position: absolute;
      right: 26px;
      top: 14px;

      &::before {
        content: '\f057';
        font-family: 'Font Awesome 5 Free';
        display: block;
        color: rgba($su-color-black, 0.7);
        font-size: 13px;
      }

      &:focus,
      &:hover {

        &::before {
          color: rgba($su-color-black, 1);
        }
      }
    }

    // The icon/button to trigger dropdown list.
    div {
      position: absolute;
      right: 0;
      top: 0;
      display: block;
      height: 100%;
      width: 18px;

      b {
        top: 12px;
        position: relative;

        &::before {
          content: '\f107';
          color: $su-color-digital-red;
          display: block;
          font-family: 'Font Awesome 5 Free';
          font-size: 17px;
        }
      }
    }
  }

  &.chosen-container-active {

    // Faking focus state since this isn't an actual field.
    .chosen-single {
      box-shadow: rgba($su-color-bright-blue, 1) 0px 0px 0px 1px, rgba($su-color-black, 0) 0px 0px 0px 0px;
    }

    &.chosen-with-drop  {
      .chosen-single {
        box-shadow: none;

        div b {

          &::before {
            content: '\f106';
          }
        }
      }
    }
  }
}

.chosen-container {
  position: relative;
  width: 100% !important;

  // Dropdown container.
  .chosen-drop {
    position: absolute;
    top: 100%;
    width: 100%;
    clip: rect(0, 0, 0, 0);
    clip-path: inset(100% 100%);
    margin-top: -1px;
    background-color: $su-color-white;
    border: 1px solid #6b7280;
  }

  &.chosen-with-drop  {

    .chosen-drop {
      clip: auto;
      clip-path: none;
    }
  }

  // Search field within dropdown.
  .chosen-search {
    @include responsive-spacing('padding', -2);

    input[type='text'] {
      background: url("#{$su-image-path}/icon-search.svg") no-repeat 97% 45%;
      background-size: 20px;
    }
  }

  // Dropdown list.
  .chosen-results {
    max-height: 250px;
    overflow-x: hidden;
    overflow-y: auto;
    cursor: text;
    position: relative;
    margin: 0 4px 4px 0;
    padding: 0 0 0 4px;

    li {
      @include responsive-spacing('padding', -2);
      list-style: none;
      margin: 0;
      word-wrap: break-word;

      &.active-result {
        display: list-item;
        cursor: pointer;
      }

      &.highlighted {
        background-color: $su-color-digital-blue;
        color: $su-color-white;
      }
    }
  }
}

// Very specifically for the button that removes an uploaded file
// from a file upload field. This just places a Font Awesome
// X-in-a-circle icon as a pseudo element and makes it red.
a.dz-remove.frm_icon_font.frm_cancel1_icon {
  color: $su-color-digital-red;
  text-decoration: none;

  &::before {
    content: '\f057';
    font-family: 'Font Awesome 5 Free';
  }
}
